export default {
  data() {
    return {}
  },
  computed: {
    UserInvitationStatuses() {
      return [{
          text: "  ",
          value: "p",
        },
        {
          text: " ",
          value: "a",
        },
        {
          text: " ",
          value: "r",
        }
      ]
    },
    passwordRules() {
      return [
        (value) =>
        /(?=.{8,})/.test(value) ||
        "      8  ",
        (value) => /(?=.*[0-9])/.test(value) || "     ",
        (value) =>
        /(?=.*[!@#$%^&*])/.test(value) ||
        "      ! @ # $ % ^ & *  ",
        (value) => /[A-Z]/.test(value) || "      ",
      ];
    },
  },
  methods: {
    sendVerificationCode(phone, callback = null) {
      this.request({
        url: this.endpoint("users/sendVerificationCode"),
        method: "post",
        data: {
          phone: phone
        },
        token: false,
        success: data => {
          callback && callback(data)
        }
      });
    },
  }

}